import { newsData } from '../components/Data';

function News() {
	return (
		<div className="content news">
			{newsData.map((news, i) => (
				<button onClick={null} key={i}>
					<div className="news-figure">
						<i className="img">
							<img className="img" src="/img/main_slide3.jpeg" alt="" />
						</i>
						<p className="caption">{news.caption}</p>
					</div>
					<h3>{news.title}</h3>
					<p className="date">{news.date}</p>
					<p className="text">{news.content}</p>
				</button>
			))}
		</div>
	);
}

export default News;
