import Router from './Router';

import { Scrollbars } from 'react-custom-scrollbars';

import ReactModal from 'react-modal';
import ModalsProvider from './modal/ModalsProvider';
ReactModal.setAppElement('#root');

function App() {
	return (
		<>
			<ModalsProvider>
				<div id="wrap">
					<Router></Router>
				</div>
			</ModalsProvider>
		</>
	);
}

export default App;
