import ReactModal from 'react-modal';

import { roomData } from '../Data';

const Room = ({ onSubmit, onClose }) => {
	// const handleClickSubmit = () => {
	// 	onSubmit();
	// };

	const handleClickCancel = () => {
		onClose();
	};
	return (
		<ReactModal isOpen className="Modal.defaultStyles">
			<div className="modal">
				<div className="modal-title">
					<h2>
						{roomData[2].en}
						<span>{roomData[2].ko}</span>
					</h2>
					<button className="modal-close" onClick={handleClickCancel}>
						<span className="hide">닫기</span>
					</button>
				</div>
				<div className="modal-content">
					<div className="roomData-figure">
						<i className="img">
							<img src="/img/room/v0.jpeg" alt="" />
						</i>
						<p className="caption">밸리 객실 내부 전경을 담은 사진</p>
					</div>
					<div>
						<h3>객실정보</h3>
						<p className="text">{roomData[2].info}</p>
					</div>
					<div>
						<h4>인원</h4>
						<p className="text">{roomData[2].personnel}</p>
					</div>
					<div>
						<h4>객실구성</h4>
						<p className="text">{roomData[2].config}</p>
					</div>
					<div>
						<h4>객실크기</h4>
						<p className="text">{roomData[2].size}</p>
					</div>
				</div>
				{/* <div className="modal-button-area">
					<button onClick={handleClickCancel}>확인</button>
					<button onClick={handleClickSubmit} className="sub">확인</button>
				</div> */}
			</div>
		</ReactModal>
	);
};

export default Room;
