import { Link, useLocation } from 'react-router-dom';

function Title() {
	// 경로이름 가져오기
	let pathName = useLocation().pathname;

	(function pathFormat() {
		pathName = pathName.substring(pathName.lastIndexOf('/') + 1);
	})();

	// 날짜 포메팅
	const today = new Date();
	const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
	return (
		<div className="title">
			<div className="pattern">
				<p>THANKS FOR COMING OUR HOME PAGE PENSIO</p>
				<img src="/img/pattern.png" alt="" />
				<p>N BONJOUR GET THE INFORMATION YOU NEED</p>
			</div>
			<div className="title-inner">
				<Link to="/" className="ad">
					홈으로가기
				</Link>
				<p className="h1">{pathName === '' ? 'pensionbonjour' : pathName}</p>
				<div className="date">
					<p className="month">{months[today.getMonth()]}</p>
					<p className="year">{today.getFullYear()}</p>
				</div>
			</div>
		</div>
	);
}

export default Title;
